import { Component, OnInit } from '@angular/core';
import {ConnectionService} from '../connection.service';
import {Employee} from '../employee';
import {Router} from '@angular/router';

@Component({
  selector: 'app-secondpage',
  templateUrl: './secondpage.component.html',
  styleUrls: ['./secondpage.component.css']
})
export class SecondpageComponent implements OnInit {

allEmployee=[];
  constructor(private connect1:ConnectionService,private _router:Router) { }

  ngOnInit() {
    this.getAllEmployee();
   
  }
getAllEmployee(){
  this.connect1.getEmployee().subscribe((data)=>{
  this.allEmployee=Array.from(Object.keys(data),k=>data[k]);
  console.log(this.allEmployee);
});
}

backer(){
  this._router.navigate(['mainpage']);

 }
deleteSome(id: number){
  console.log(id);
  this.connect1.deleteEmployee(id).subscribe(
(data:Employee)=>{
  this.getAllEmployee();
}

  );
}

}
