import { Component, OnInit } from '@angular/core';
import {FormGroup,FormControl,Validators} from '@angular/forms';
import {ConnectionService} from '../connection.service';
import {Employee} from '../employee';
import {Router} from '@angular/router';
@Component({
  selector: 'app-mainpage',
  templateUrl: './mainpage.component.html',
  styleUrls: ['./mainpage.component.css']
})
export class MainpageComponent implements OnInit {
name;
address;
contactno;
email;
formdata;

employee: Employee=new Employee();


  constructor(private connect:ConnectionService,private _router:Router) { }

  ngOnInit() {
this.formdata=new FormGroup({
name:new FormControl(""),
address:new FormControl(""),
contactno:new FormControl(""),
email:new FormControl("")

});



  }
onClickSubmit(data){
  this.name=data.name;
  this.email=data.email;
  this.contactno=data.contactno;
  this.address=data.address;

this.employee=new Employee();

this.employee.Employee_name=data.name;
this.employee.Employee_mail=data.email;
this.employee.Employee_contactno=this.contactno;
this.employee.Employee_addr=this.address;
this.connect.postEmployee(this.employee).subscribe(data=>console.log(data),error=>console.log(error));
this._router.navigate(['secondpage']);

}
}
